<template>
  <div>
    <div class="mt20">
      <span class="pad-r">我的申请</span>
      <el-radio-group v-model="status" @change="changeSatatus">
        <el-radio-button :label=item.value v-for="item in whiteAuthStatus" :key="item.value">{{item.label}}</el-radio-button>
      </el-radio-group>
      <el-button type="primary" class="fr" @click="addNewWhiteApp">提交新申请</el-button>
    </div>
    <el-table
      class="mt10"
      border
      :data="listData"
      v-loading="loading">
      <el-table-column prop="title" label="申请人"></el-table-column>
      <el-table-column prop="created_at" label="申请时间"></el-table-column>
      <el-table-column prop="auditor" label="审核人"></el-table-column>
      <el-table-column prop="audit_at" label="审核时间"></el-table-column>
      <el-table-column prop="status" label="审核结果"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="doOperate(scope.row, item)" v-for="item in (scope.row.operateList || [])" :key="item.operate_status">{{item.label}}</el-button>
          <el-button type="text" size="small" @click="doDetail(scope.row)">查看详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pag-block" v-if="listData.length> 0">
      <div class="fl">每页{{pageSize}}条，共 {{total}} 条记录 第 {{page}}/{{total}} 页</div>
      <el-pagination
        class="fr"
				@current-change="getListbyPage"
			  :current-page="page"
			  background
			  layout="prev, pager, next, jumper"
			  :page-size="pageSize"
			  :total="total">
			</el-pagination>
    </div>
    <!-- 申请详情 -->
    <el-dialog
      title="申请详情"
      :visible.sync="dialogVisible"
      width="800px">
      <el-table
        border
        :data="detailTable">
        <el-table-column prop="name" label="姓名"></el-table-column>
        <el-table-column prop="uid" label="ID"></el-table-column>
        <el-table-column prop="title" label="职务说明"></el-table-column>
        <el-table-column prop="rule_type" label="开通级别"></el-table-column>
        <el-table-column prop="user_type" label="用户类型"></el-table-column>
      </el-table>
    </el-dialog>
    <!-- 提交新申请 -->
    <white-app-dialog ref="whiteAppDialog" @add-white-callback="addWhiteCallback"></white-app-dialog>
  </div>
</template>

<script>
import api from '@/api'
import { mapGetters } from 'vuex';
import validate from '@/assets/js/validate'
import typesOfRepresent from '@/assets/js/typesOfRepresent'
import WhiteAppDialog from '@/components/applicationManage/WhiteAppDialog.vue'

export default {
  components: {
    WhiteAppDialog
  },
  watch: {

  },
  data() {
    return {
      whiteAuthStatus: typesOfRepresent.whiteAuthStatus,
      status: 0,
      listData: [],
      loading: false,
      page: 1,
      pageSize: 10,
      total: 0,
      dialogVisible: false,
      detailTable: []
    };
  },
  computed: {
    ...mapGetters(['getUserInfo', 'getAdminLevel'])
  },
  created() {
    this.getList();
  },
  mounted() {

  },
  methods: {
    getList() {
      console.log(this.getAdminLevel)
			this.loading = true;
			this.$axios.get(api.whiteApplyListApi, { params: {
					page: this.page -1,
          page_size: this.pageSize,
          status: this.status,
          proposer: this.getAdminLevel === 4? null: this.getUserInfo.name
				}
			}).then(res => {
        this.loading = false;
        this.listData = ((res.data.result || {}).list) || []
				this.total = Number((res.data.result || {}).total || 0)
			});
    },
    doOperate(row, item){
      // 撤回取消操作不提示
      if (item.operate_status === 4 || (item.operate_status === 1)) {
        this.operateAuth({
          apply_seq: row.apply_seq,
          status: item.operate_status,
          auditor: this.getUserInfo.name
        })
        return
      }
      this.$confirm(`是否确认${item.label}申请，${item.label}后${item.operate_status === 2? '立即生效，不可撤回': '可以撤回'}`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.operateAuth({
          apply_seq: row.apply_seq,
          status: item.operate_status,
          auditor: this.getUserInfo.name
        })
      }).catch((err) => {
        console.log(err)
      });
    },
    // 1未审核（撤回），2审核通过， 3审核不通过, 4取消
    operateAuth(params) {
      this.loading = true;
      this.$axios.post(api.whiteAuditApi, params).then(res => {
        this.loading = false;
        if (res.data.errorNo && res.data.errorMsg) {
          let a = res.data.errorMsg.replace(/,/g, "<br/>")
          this.$message.warning({
            dangerouslyUseHTMLString: true,
              message: `<pre>${a}</pre>`
          });
        } else {
          this.$message.success('操作成功！');
        }
        this.getList();
			});
    },
    doDetail(row) {
      this.detailTable = [];
      this.dialogVisible = true;
      this.$axios.get(api.whiteDetailApi, {params: {apply_seq: row.apply_seq}}).then(res => {
        this.detailTable = res.data.result;
      });
    },
    getListbyPage(page) {
			this.page = page;
			this.getList();
    },
    // 提交新申请
    addNewWhiteApp() {
      this.$refs.whiteAppDialog.reInit();
    },
    // 添加白名单成功后回调
    addWhiteCallback() {
      this.page = 1;
      this.getList();
    },
    changeSatatus() {
      this.page = 1;
      this.getList();
    }
  }
};
</script>

<style lang="scss" scoped>
.pad-r{
  padding-right: 20px;
}
</style>



