<template>
  <el-dialog
    title="申请开通白名单"
    :visible.sync="dialogVisible"
    width="1100px">
    <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="80px" class="demo-dynamic" :inline="true" >
      <div v-for="(item, index) in dynamicValidateForm.list" :key="index">
        <el-form-item
          label-width="60px"
          :label="'姓名'"
          :prop="'list.' + index + '.name'"
          :rules="{required: true, message: '姓名不能为空', trigger: 'blur'}">
          <el-input v-model="item.name" class="width100" maxLength="6"></el-input>
        </el-form-item>
        <el-form-item
          label-width="40px"
          :label="'ID'"
          :prop="'list.' + index + '.uid'"
          :rules="ruleID">
          <el-input v-model="item.uid" class="width100"></el-input>
        </el-form-item>
        <el-form-item
          :label="'职务说明'"
          :prop="'list.' + index + '.title'"
          :rules="{required: true, message: '职务说明不能为空', trigger: 'blur'}">
          <el-input v-model="item.title" maxLength="10"></el-input>
        </el-form-item>
        <el-form-item
            :label="'开通级别'"
            :prop="'list.' + index + '.rule_type'"
            :rules="{required: true, message: '请选择开通级别', trigger: 'blur'}">
          <el-select v-model="item.rule_type" placeholder="请选择" class="width100">
            <el-option
              v-for="item in ruleType"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
            :label="'用户类型'"
            :prop="'list.' + index + '.user_type'"
            :rules="{required: true, message: '请选择用户类型', trigger: 'blur'}">
          <el-select v-model="item.user_type" placeholder="请选择" class="width100">
            <el-option
              v-for="item in userType"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click.prevent="removeRow(item)" type="text">删除</el-button>
        </el-form-item>
      </div>
    </el-form>
    <div class="align-center">
      <el-button @click.prevent="addRow" type="text">添加</el-button>
      <div class="opt">
        <el-button @click.prevent="submitForm()" type="primary" :loading="loading">提交</el-button>
        <el-button @click.prevent="dialogVisible=false">取消</el-button>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import api from '@/api'
import { mapGetters } from 'vuex';
import validate from '@/assets/js/validate'
import typesOfRepresent from '@/assets/js/typesOfRepresent'

export default {
  components: {

  },
  watch: {

  },
  data() {
    return {
      loading: false,
      dialogVisible: false,
      ruleID: [
        { required: true, message: '请输入ID'},
        { validator: validate.checkInviterId, trigger: 'blur' }
      ],
      ruleType: typesOfRepresent.ruleType,
      userType: typesOfRepresent.userType,
      dynamicValidateForm: {
        list: [{
          name: '',
          rule_type: '',
          title: '',
          uid: '',
          user_type: ''
        }]
      }
    };
  },
  computed: {
    ...mapGetters(['getUserInfo'])
  },
  created() {

  },
  mounted() {

  },
  methods: {
    reInit() {
      this.dialogVisible = true;
      this.dynamicValidateForm = {
        list: [{
          name: '',
          rule_type: '',
          title: '',
          uid: '',
          user_type: ''
        }]
      };
      this.$refs['dynamicValidateForm'] && this.$refs['dynamicValidateForm'].resetFields();
    },
    submitForm() {
      this.$refs['dynamicValidateForm'].validate((valid) => {
        if (valid) {
          this.doAddWhite();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    doAddWhite() {
      let tempArr = [];
      let id = '';
      for (let index = 0; index < this.dynamicValidateForm.list.length; index++) {
        const element = this.dynamicValidateForm.list[index];
        if (tempArr.indexOf(element.uid) >= 0) {
          id = element.uid;
          break;
        } else {
          tempArr.push(element.uid)
        }
      }

      if(id) {
        this.$message.warning(`ID ${id}重复申请！`);
        return
      }
      this.loading = true
			this.$axios.post(api.whiteCreateApi, {
        info: {
          proposer: this.getUserInfo.name
        },
        list: this.dynamicValidateForm.list
      }).then(res => {
        this.loading = false;
        this.$message.success('添加白名单成功！');
        this.dialogVisible = false;
        this.$emit('add-white-callback');
      }).catch((err) => {
        this.$message.closeAll()
        this.loading = false;
        err = err.replace(/,/g, "<br/>")
        this.$message.warning({
          dangerouslyUseHTMLString: true,
            message: `<pre>${err}</pre>`
        });
      });
    },
    resetForm(formName) {
      this.$refs['dynamicValidateForm'].resetFields();
    },
    removeRow(item) {
      var index = this.dynamicValidateForm.list.indexOf(item)
      if (index !== -1) {
        this.dynamicValidateForm.list.splice(index, 1)
      }
    },
    addRow() {
      this.dynamicValidateForm.list.push({
        name: '',
        rule_type: '',
        title: '',
        uid: '',
        user_type: ''
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.width100{
  width: 100px;
}
.opt{
  border-top: 1px solid #eee;
  padding-top: 40px;
  margin-top: 20px;
}
</style>



